import storage from '@/utils/storages/index.js'

export const setToken = (data) => storage.set('token', data)

export const getToken = () => storage.get('token')

export const removeToken = () => storage.remove('token')

export const setIsLogin = (data) => storage.set('is_login', data)

export const getIsLogin = () => storage.get('is_login')

export const removeIsLogin = () => storage.remove('is_login')

let userInfoKey = 'globalUserSearchInfo'
export function getUserInfoKeySync() {
  return uni.getStorageSync(userInfoKey)
}
